import React from 'react'
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import ProgressBar from 'react-bootstrap/ProgressBar';
import BottomBackNextBtn from 'views/mobileView/components/atoms/BottomBackNextBtn';
import db from 'libs/db';
import DOBInputMask from 'views/mobileView/components/atoms/DOBInputMask';
import { Chip } from '@mui/material';

const NomineeDetails = (props) => {

	const [addNominee, setAddNominee] = React.useState('1')
	const [noName, setNoName] = React.useState('')
	const [noRelation, setNoRelation] = React.useState('')
	const [noDob, setNoDob] = React.useState('')
	const [noGuardian, setNoGuardian] = React.useState('')
    const [dobv, setDobv] = React.useState('');
	const [loading,setLoading] =React.useState(true)
	const [nextDisabled, setNextDisabled] = React.useState(false)
	const data = [
		{ key: '0', value: 'Yes' },
		{ key: '1', value: 'No' }
	]
	const chipset = [
		{ "key": "Father", "name": "Father" },
		{ "key": "Mother", "name": "Mother" },
		{ "key": "Spouse", "name": "Spouse" },
		{ "key": "Son", "name": "Son" },
		{ "key": "Daughter", "name": "Daughter" },
	]
	React.useEffect(async() => {
		if(!props.location.state?.taxStatus){
			props.history.push('/activate-direct-mf/tax-status')
		} else {
			await db.get(db.model.userData).then((u) => {
				if (u) {
					if (u.Nominee) {
						setAddNominee(u.Nominee.addNominee || '')
						setNoName(u.Nominee.noName || '')
						setNoRelation(u.Nominee.noRelation || '')
						setNoDob(u.Nominee.noDob || '')
						setNoGuardian(u.Nominee.noGuardian || '')
					}
				}
			})
			setLoading(false)
		}
	}, []);

	React.useEffect(() => {
		checkValidation();
	}, [addNominee, noName, noRelation, noDob, noGuardian]);

	const checkValidation = () => {
		setNextDisabled(true)
		if (addNominee === '1') {
			setNextDisabled(false);
		} else {
			if (noName.length > 2 && noRelation !== '' && noDob.length === 14) {
				if (getAge(noDob) < 18 && noGuardian !== '') {
					setNextDisabled(false);
				} else if (getAge(noDob) > 18) {
					setNextDisabled(false);
				}
			}
		}
	};
	function getAge(dateString) {
		dateString = dateString.split(" / ").reverse().join("/");
		var today = new Date();
		var birthDate = new Date(dateString);
		var age = today.getFullYear() - birthDate.getFullYear();
		var m = today.getMonth() - birthDate.getMonth();
		if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
			age--;
		}
		return age;
	}
	return (
		<div className='mainContainer'>
			<MainHeader
				header="Activate mutual funds"
				navigation={props.history}
			/>
			<ProgressBar now={70} className="progress-bar-custom" />
			<div className="dataContainer" style={{paddingTop:'42px'}}>
				<div className='header1dark'>
					Do you wish to add a nominee?
				</div>
				{data.map((obj) => (
					<div key={obj.key} style={{ display: 'flex', flexDirection: 'row', marginTop: "6%" }}>
						<div className="radio-item">
							<input
								className='normalInput'
								type="radio"
								id={obj.key}
								name="ritem"
								value={obj.key}
								onChange={(val) => {
									setAddNominee(val.target.value)
									console.log(val.target.value)
								}}
								checked={addNominee === obj.key}
							/>
							<label className="bodyTextDark radio-label" htmlFor={obj.key}>{obj.value}</label>
						</div>
					</div>
				))}
				{addNominee === '0' ?
					<>
						<div className='header1dark' style={{marginTop:"6%"}}>Enter nominee details</div>
                        <div style={{ marginTop: "6%" }}>

						<FloatingTextInput
							type="text"
							id="full name"
							style={{textTransform:'capitalize'}}
							label="Full name"
							value={noName}
							onChange={(text) => {
								setNoName(text.target.value)
							}}
						/>
						</div>
                        <div style={{ marginTop: "6%" }}>
						<FloatingTextInput
							value={noRelation}
							type="text"
							style={{textTransform:'capitalize'}}
							id="relation"
							label="Relationship with you"
							onBlur={() => checkValidation()}
							onChange={(text) => {
								setNoRelation(text.target.value)
							}}
						/>
						</div>
						<div className="hiddden-scrollbar">
							{chipset.map((item) => {
								return (
										<Chip
											className={noRelation===item.key?"normalChip selected":"normalChip"}
											label={item.name}
											onClick={() => {
												setNoRelation(item.name)
											}} />
								)
							})}
						</div>
                        <div style={{ marginTop: "6%" }}>
						{loading?'':<DOBInputMask
							dateCheck
							label="Date of birth"
							floatingLabel={true}
							defaultVal={noDob}
							dobof={props.location.state.selection}
							minAge={0}
							maxAge={200}
							dateValidation={(dobvv) => {
								setDobv(dobvv)
							}}
							setDOB={(dobb) => {
								setNoDob(dobb);
							}}
							onBlur={() => checkValidation()}
						/>}
						</div>
                        <div style={{ marginTop: "6%" }}>
						{getAge(noDob) < 19 ?
							<FloatingTextInput
								type="text"
								id="guard name"
								style={{textTransform:'capitalize'}}
								label="Legal guardian name"
								onBlur={() => checkValidation()}
								value={noGuardian}
								onChange={(text) => {
									setNoGuardian(text.target.value)
								}}
							/> : null}
							</div>
					</>
					:
					""
				}
			</div>
			<div className='footer'>
				<BottomBackNextBtn
					nextDisabled={nextDisabled}
					navigation={props.history}
					buttonText="Confirm"
					next="/activate-direct-mf/bank-details"
					params={props.location.state}
					saveData={{
						Nominee: {
							addNominee,
							noName,
							noRelation,
							noDob,
							noGuardian,
						}
					}} />
			</div>
		</div>
	)
}

export default NomineeDetails
